@import url(https://fonts.googleapis.com/css?family=Montserrat:600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

form{
  font: 500 1rem/1.1 'Montserrat', sans-serif;
}
[type=radio] { 
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
  
}

/* IMAGE STYLES */
[type=radio] + img {
  cursor: pointer;
  width: 5em;
  margin-right: 0.7em;
  margin-left: 0.7em;
}

/* CHECKED STYLES */
[type=radio]:checked + img {
  -webkit-filter: invert(12%) sepia(41%) saturate(6285%) hue-rotate(323deg) brightness(96%) contrast(101%);
          filter: invert(12%) sepia(41%) saturate(6285%) hue-rotate(323deg) brightness(96%) contrast(101%);
 }

